import * as React from 'react';
import { Routes, Route } from 'react-router-dom';

const CartCreateView = React.lazy(() => import('./views.Create'));
const CartUpdateView = React.lazy(() => import('./views.Update'));
const CartListView = React.lazy(() => import('./views.List'));

export default function CartRoutes() {
  return (
    <Routes>
      <Route path=":cartSlug/" element={<CartUpdateView />} />
      <Route path="cart/" element={<CartCreateView />} />
      <Route index element={<CartListView />} />
    </Routes>
  );
}

